<template>
	<view class="content">
		<div class='mytitle flex-column top start u-skeleton'>
			<div class='portrait'>
				<cover-image  class="u-skeleton-circle" :src="myInfo.Avatar?myInfo.Avatar:'/static/memager.png'"></cover-image>
			</div>	
			<div class='flex-column middle start portext wf'>
				<div class="text lh8 f7w flex-row start ">
					{{myInfo.nickname}}
					<div class='vipClass flex-row'>
						<span class='vipimages'>
							<u-image
							height="36rpx" 
							src="/static/zs.png"></u-image>
						</span>
						<span>尊贵访客</span>
					</div>
				</div>
				<div class="text lh8 f6w u-skeleton-fillet">HI~欢迎来到金地线上售楼处欢迎你</div>
			</div>
		</div>
		<u-gap height="18" bg-color="#f7f7f7"></u-gap>
		<view class="centerContent">
			<u-cell-group :border='false'>
				<u-grid :col="col" :border='false'>
					<u-grid-item @click='openinfo("/pages/my/wxlogin")' bg-color='#1c2a39'>
						<cover-image class="grid-img" src="../../static/icon/活动.png"></cover-image>
						<view class="grid-text-z">我的活动</view>
					</u-grid-item>
					<u-grid-item @click='openinfo("/pages/my/collection")' bg-color='#1c2a39'>
						<cover-image class="grid-img" src="../../static/icon/预约.png"></cover-image>
						<view class="grid-text-z">预约看房</view>
					</u-grid-item>
					<u-grid-item @click='openinfo("/pages/my/draft")' bg-color='#1c2a39'>
						<cover-image class="grid-img" src="../../static/icon/名片.png"></cover-image>
						<view class="grid-text-z">顾问名片</view>
					</u-grid-item>
				</u-grid>
			</u-cell-group>
		</view>
	</view>
</template>

<script>
	import loginMpWx from '@/common/oyyl-mixin/login-mp-wx.js'
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		mixins: [loginMpWx],
		data() {
			return {
				activity:[],//活动
				myInfo:{},
				col:3,
			}
		},
		onLoad() {
			this.init();
		},
		methods: {
			init(){
				this.AkxhHdlists();
				this.myInfo = this.mpWxGetUserInfo();
				// this.mpWxLogin();
				console.log(myInfo);
			},
			// 微信小程序登录
			mpWxLogin(userInfoData) {
				this.mpWxGetUserInfo(userInfoData, () => {
					this.$util.msg('登陆成功');
					setTimeout(() => {
						uni.navigateBack();
					}, 1000)
				})
			},
			openinfo(url) {
			    uni.navigateTo({
			     url: url,
			    });
			},
			AkxhHdlists(){
				//用户列表
				AkxhHd('/catalyzer/AkxhUser/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						this.activity = result[0];
						this.activity.img = this.imgURL + result[0].img;
					}
				})
			}
		}
	}
</script>

